/**
 * 
 *
 * browser.versions.ios IOS
 */
export const browser = () => {
  if (navigator) {
    const u = navigator ? navigator.userAgent : '';
    return {
      trident: u.indexOf('Trident') > -1, // IE
      presto: u.indexOf('Presto') > -1, // opera
      webKit: u.indexOf('AppleWebKit') > -1, // 
      gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') === -1, // 
      mobile: !!u.match(/AppleWebKit.*Mobile.*/), // 
      ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios
      android: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1, // android
      iPhone: u.indexOf('iPhone') > -1, // iPhoneQQHD
      iPad: u.indexOf('iPad') > -1, // iPad
      webApp: u.indexOf('Safari') === -1, // web
      weixin: u.indexOf('MicroMessenger') > -1, // 
      qq: u.match(/\sQQ/i) === ' qq', // QQ
    };
  }
  return {};
};

export const isMobile = () => {
  const ua = navigator.userAgent.toLowerCase();
  const is_mobile =
    ua.match(
      /(ipod|iphone|android|coolpad|mmp|smartphone|midp|wap|xoom|symbian|j2me|blackberry|wince)/i,
    ) !== null;
  return is_mobile;
};

export const isPc = () => {
  const ua = navigator.userAgent;
  if (
    /AppleWebKit.*Mobile/i.test(ua) ||
    /MIDP|SymbianOS|NOKIA|SAMSUNG|LG|NEC|TCL|Alcatel|BIRD|DBTEL|Dopod|PHILIPS|HAIER|LENOVO|MOT-|Nokia|SonyEricsson|SIE-|Amoi|ZTE/.test(
      ua,
    )
  ) {
    return !/Android|webOS|iPhone|iPod|BlackBerry|iPad/i.test(ua);
  }
  return true;
};

export const isApp = () => {
  const ua = navigator.userAgent.toLowerCase();
  if (ua.indexOf('kkbmobile') !== -1) {
    return true;
  }
  return false;
};
